@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);

* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}
body {
  height: 100%;
  width: 100%;
}
.app {
  display: flex;
}

.chat {
  display: flex;
  flex-direction: column;
  flex: 0.75 1;
  background-color: #36393f;
  height: 100vh;
}

.chat__messages {
  flex: 1 1;
  overflow: scroll;
  display: flex;
  overflow-x: hidden;
  flex-direction: column-reverse;
}
/* .chat__messages::-webkit-scrollbar {
  display: none;
} */
.chat__messages::-webkit-scrollbar {
  width: 12.5px;
}
.chat__messages::-webkit-scrollbar-track {
  background: rgba(235, 235, 236, 0.1);
}
.chat__messages::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
}
.chat__messages::-webkit-scrollbar-corner {
  background: transparent;
}
.chat__input {
  color: lightgray;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 6px;
  border-radius: 5px;
  margin: 20px;
  background-color: #474b53;
}
form {
  color: #fff !important;
}
.chat__search {
  padding: 0px 10px;
  background: transparent;
  border: none;
  width: 100%;
  outline-width: 0;
  font-family: "Poppins", sans-serif !important;
  color: #fff !important;
  font-size: 12px;
}

.chat__input > form {
  flex: 1 1;
}

.chat__inputButton {
  display: none;
}

.chat__inputIcons > .MuiSvgIcon-root {
  padding: 3px 5px;
}

.chatHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: gray;
  padding: 1px;
  border-bottom: 1px solid #232323;
  background: #36393f;
}

.chatHeader__hash {
  color: gray;
  font-size: 19px;
  padding: 10px;
}

.chatHeader__left > h4 {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #fff;
}

.chatHeader__right {
  display: flex;
  align-items: center;
  flex: 0.5 1;
  justify-content: space-between;
}

.chatHeader__right > .MuiSvgIcon-root {
  padding: 7px;
  cursor: pointer;
}

.chatHeader__right > .MuiSvgIcon-root:hover {
  color: white;
}

.chatHeader__search {
  display: flex;
  align-items: center;
  color: #aaa;
  background-color: #2f3135;
  border-radius: 3px;
  padding: 3px;
  font-family: "Poppins", sans-serif !important;
  width: 300px;
  height: 30px;
  font-size: 6px;
  margin: 3px;
}

.chatHeader__search > input {
  background: transparent;
  outline-width: 0;
  color: white;
  border: none;
}

.message {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  transition: 0.25s;
  color: white;
}
.message h4 {
  font-weight: 300;
}

.message__info {
  margin-left: 20px;
}

.message__timestamp {
  color: gray;
  margin-left: 10px;
  font-size: x-small;
}
.message:hover {
  background: #32353b;
}

.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.25 1;
  height: 100vh;
  background-color: #2f3136;
}

.sidebar__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12.5px;
  background-color: #2f3136;
  color: white;
  border-bottom: 1px solid #232323;
}

.sidebar__channels {
  flex: 1 1;
  transition: 0.25s;
  overflow: scroll;
  overflow-x: hidden;
}
.sidebar__channels::-webkit-scrollbar {
  width: 10px;
}
.sidebar__channels::-webkit-scrollbar-track {
  background: rgba(235, 235, 236, 0.1);
}
.sidebar__channels::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
}
.sidebar__channels::-webkit-scrollbar-corner {
  background: transparent;
}
.sidebar__addChannel {
  cursor: pointer;
}

.sidebar__addChannel:hover {
  color: white;
}

.sidebar__channelsHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #2f3135;
  color: gray;
  transition: 0.25s;
}

.siderbar__header {
  display: flex;
  align-items: center;
}

.sidebar__voice,
.sidebar__profile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: gray;
  background-color: #2f3135;
  padding: 10px;
  border-top: 1px solid #555;
}

.sidebar__voiceInfo {
  flex: 1 1;
  padding: 2px;
}

.sidebar__voiceIcon {
  color: #4fb185;
}

.sidebar__voiceInfo > h5 {
  color: #4fb185;
}

.sidebar__voiceIcons > .MuiSvgIcon-root {
  padding: 3px 7.5px;
}

.sidebar__profileInfo {
  flex: 1 1;
  padding: 2px 6px;
}

.sidebar__profileInfo > h5 {
  color: white;
}

.sidebar__profileIcons > .MuiSvgIcon-root {
  padding: 3px 7.5px;
  cursor: pointer;
}
.sidebar__profileIcons > .MuiSvgIcon-root:hover {
  color: #fff;
}

.MuiIconButton-root {
  color: gray !important;
}
.MuiIconButton-root:hover {
  color: #fff !important;
}
/* .siderbar__header > .MuiIconButton-root {
  height: 45px;
  width: 45px;
} */

.sidebarChannel > h5 {
  transition: 0.25s;
}
.sidebarChannel:hover > h5 {
  color: white;
  background-color: #393c42;
}

.sidebarChannel > h5 {
  display: flex;
  padding-left: 15px;
  align-items: center;
  background-color: #2f3135;
  color: gray;
  cursor: pointer;
}

.sidebarChannel__hash {
  font-size: 16px;
  padding: 8px;
}

.login {
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100%;
  background: #f1f2f5;
}
.login__cont {
  display: grid;
  place-items: center;
}

.login__cont button {
  width: 300px;
  text-transform: none;
  background-color: #738adb;
  border: 1px solid #738adb;
  color: #eff2f5;
  font-size: 16px;
  transition: 0.5s;
}
.login__cont button:hover {
  background-color: transparent;
  color: #738adb;
}
.login__cont img {
  height: 130px;
}
.login > div > img {
  object-fit: contain;
}

